@charset "UTF-8";
.m-question-cover {
  transition: opacity 1s 0ms, z-index 0ms 0ms;
  background-color: #FAFAFA;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  opacity: 1;
  z-index: 999;
  background-image: url("../images/loading.gif");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.m-question-cover::after {
  position: absolute;
  left: 50%;
  top: 45%;
  opacity: 0;
  font-size: x-large;
}

.config-complete .m-question-cover {
  background-image: none;
}

.unavailable.collapse {
  display: none;
}

.unavailable .m-question-cover::after {
  opacity: 1;
  color: #9CADCB;
  content: "âƒ ";
}

.cover-off .m-question-cover {
  transition: opacity 1s 0ms, z-index 0ms 1s;
  opacity: 0;
  pointer-events: none;
  z-index: -999;
}